<template>
    <mindmap style="height: 100%" v-model="data"></mindmap>
</template>

<script>
import mindmap from '@hellowuxin/mindmap'

export default {
  components: { mindmap },
  data: () => (
    {
    data: [{
      "name":"D3",
      "children": [
        {
          "name":"",
          "children": [
            { "name":"HTML & CSS" },
            { "name":"JavaScript" },
          ]
        },
        {
          "name":"",
          "_children": [
            { "name": "" }
          ]
        },
        {
          "name":"",
          "left": true
        }
      ]
    }]
  })
}
</script>



